* {
  background-color: lightblue;
}

.App {
  width: 500px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.heading {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 50px 0;
  padding-top: 0;
  justify-content: space-evenly;
  margin: 30px 0;
  text-align: center;
}

.btn-del {
  background-color: red;
  color: black;
  outline: none;
  border: 3px solid black;
  padding: 0.2rem 0.35rem;
  border-radius: 999px;
}

.btn-main {
  background-color: blue;
  color: lightgrey;
  padding: 1rem 2rem;
  border: 2px solid lightseagreen;
  border-radius: 20px;
  outline: none;
}
.btn-main:hover,
input[type="submit"]:hover {
  border-color: green;
}
.btn-main:active,
input[type="submit"]:hover {
  border-color: green;
  border-width: 4px;
}

form,
.list {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 50px 0;
  justify-content: space-between;
  align-items: center;
}

#box {
  margin-bottom: 10px;
  text-align: center;
  border: solid black 3px;
  background-color: aqua;
  width: 200px;
  border-radius: 5px;
}

.box {
  background-color: aqua;
}

input[type="submit"] {
  background-color: blue;
  color: lightgrey;
  padding: 0.5rem 1rem;
  border: 2px solid lightseagreen;
  border-radius: 20px;
  outline: none;
}

input {
  background-color: lightgreen;
  margin-bottom: 10px;
  height: 30px;
  width: 200px;
  border-radius: 20px;
  text-align: center;
}

.btn-aquarium {
  color: lightgrey;
  padding: 0.5rem 1rem;
  border: 2px solid lightseagreen;
  border-radius: 20px;
  outline: none;
}
.btn-color-red {
  background-color: red;
}
.btn-color-green {
  background-color: green;
}
.btn-aquarium:hover {
  border-color: black;
}
.btn-aquarium:hover {
  border-width: 4px;
}
